.l-put {
  height: 1.28rem;
  font-size: 0.426667rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-align: center;
  align-items: center;
}

.l-put .label {
  width: 2.4rem;
  padding-left: 0.133333rem;
  color: #000;
}

.l-put input {
  width: 100%;
  text-align: center;
  background: none;
  outline: none;
  border: 0;
  caret-color: blue;
  padding: 0.266667rem 0;
}


